import { CloseOutlined } from "@ant-design/icons";
import { BasketItemControl } from "components/basket";
import { ImageLoader } from "components/common";
import Box from "components2/Box";
import { displayMoney } from "helpers/utils";
import PropType from "prop-types";
import React from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { removeFromBasket } from "redux/actions/basketActions";

import styled from "styled-components";

const BasketItem = ({ product }) => {
  const dispatch = useDispatch();
  const onRemoveFromBasket = () => dispatch(removeFromBasket(product.id));

  return (
    <Container>
      <BasketItemControl product={product} />
      <Wrapper>
        <Box
          position="relative"
          width="90px"
          height="90px"
          marginRight="m-size"
        >
          <ImageLoader
            alt={product.name}
            className="basket-item-img"
            src={product.image}
          />
        </Box>
        <Box flexGrow={1}>
          <Link
            to={`/product/${product.id}`}
            onClick={() => document.body.classList.remove("is-basket-open")}
          >
            <h4 className="underline basket-item-name">{product.name}</h4>
          </Link>
          <div className="basket-item-specs">
            <div>
              <span className="spec-title">Quantity</span>
              <h5 className="my-0">{product.quantity}</h5>
            </div>
            <div>
              <span className="spec-title">Size</span>
              <h5 className="my-0">{product.selectedSize} mm</h5>
            </div>
            <div>
              <span className="spec-title">Color</span>
              <div
                style={{
                  backgroundColor:
                    product.selectedColor || product.availableColors[0],
                  width: "15px",
                  height: "15px",
                  borderRadius: "50%",
                }}
              />
            </div>
          </div>
        </Box>
        <Box marginRight="2rem">
          <h4 className="my-0">
            {displayMoney(product.price * product.quantity)}
          </h4>
        </Box>
        <button
          className="basket-item-remove button button-border button-border-gray button-small"
          onClick={onRemoveFromBasket}
          type="button"
        >
          <CloseOutlined />
        </button>
      </Wrapper>
    </Container>
  );
};

const Container = styled.div`
  display: flex;
  align-items: center;
  border: 1px solid ${(props) => props.theme.colors["border-color"]};
  margin-bottom: ${(props) => props.theme.space["s-size"]};
  /* @include bezier-transition(); */
  animation: slide-up 0.5s ease;
`;

const Wrapper = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  padding: ${(props) => `0 ${props.theme.space["s-size"]}`};

  display: grid;
  grid-template-columns: 100px 1fr 80px 40px;
`;

BasketItem.propTypes = {
  product: PropType.shape({
    id: PropType.string,
    name: PropType.string,
    brand: PropType.string,
    price: PropType.number,
    quantity: PropType.number,
    maxQuantity: PropType.number,
    description: PropType.string,
    keywords: PropType.arrayOf(PropType.string),
    selectedSize: PropType.string,
    selectedColor: PropType.string,
    imageCollection: PropType.arrayOf(PropType.string),
    sizes: PropType.arrayOf(PropType.number),
    image: PropType.string,
    imageUrl: PropType.string,
    isFeatured: PropType.bool,
    isRecommended: PropType.bool,
    availableColors: PropType.arrayOf(PropType.string),
  }).isRequired,
};

export default BasketItem;
